
import React, { Component } from 'react'
import ReactTableContainer from '../containers/Table/ReactTableContainer'
const columns = [
    {
      Header: 'Title',
      accessor: 'title',
      headerStyle: 'col-xs-3',
      style: 'col-xs-3',
    },
    {
      Header: 'Short Desc',
      accessor: 'shortDesc',
      headerStyle: 'col-xs-3',
      style: 'col-xs-3',
    },
    {
        Header: 'shortDesc',
        accessor: 'shortDesc',
        headerStyle: 'col-xs-2',
        style: 'col-xs-2',
      },
      {
        Header: 'title',
        accessor: 'title',
        headerStyle: 'col-xs-3',
        style: 'col-xs-3',
      },
      {
        Header: 'title',
        accessor: 'title',
        headerStyle: 'col-xs-1',
        style: 'col-xs-1',
      },
    
  ];
export default class GalleryDataList extends Component {
     
    state = {
        isLoading: true ,
        galleryList:this.props.galleryList
      }
      
    render() { 
      let galleryList=this.props.galleryList;
      let page=this.props.page;
        return (
               <ReactTableContainer columns={columns}  content={galleryList} page={page}></ReactTableContainer>


        )
    }
}
